@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

body {
	font-family: 'Lato', sans-serif;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
}

/* HEADER STYLES */
.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 20px;
	background-color: #fff;
}

.header nav {
	font-size: 12px;
	text-transform: uppercase;
}

.logo img {
	max-height: 50px;
}

.navigation-wrapper {
	display: none;
	align-items: center;
}

.navigation ul {
	list-style: none;
	margin: 0;
	padding: 0;
	display: flex;
}

.navigation ul li {
	margin-right: 20px;
}

.navigation ul li:last-child {
	margin-right: 0;
}

.navigation ul li a {
	text-decoration: none;
	color: #333;
	font-weight: bold;
	padding: 10px;
}

.navigation ul li a:hover {
	text-decoration: underline;
}

.contact-info {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	color: #333;
	margin-left: 20px;
}

.page-header__contacts-item {
	display: flex;
	align-items: center;
	text-decoration: none;
	color: #333;
	padding: 0 0 5px;
}

.page-header__contacts-item:hover {
	color: blue;
}

.page-header__contacts-item svg {
	width: 16px;
	height: 12px;
	fill: currentColor;
	margin-right: 5px;
}

@media screen and (min-width: 1000px) {
	.navigation-wrapper {
		display: flex;
	}
}

/* FOOTER STYLES */
.footer {
	text-align: center;
	padding: 20px;
	background-color: #fff;
}

.footer img {
	max-width: 150px;
}

@media screen and (min-width: 1200px) {
	.logo img {
		max-height: 75px;
	}
	.footer img {
		max-width: 250px;
	}
}

/* Hamburger */
.navmodal_navigation-wrapper {
	display: none;
	justify-content: space-between;
	align-items: center;
	padding: 10px;
	background-color: #fff;
	z-index: 2;
}

.navmodal {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.7);
	z-index: 1;
}

.navmodal-content {
	background-color: #fefefe;
	margin: 15% auto;
	padding: 20px;
	border: 1px solid #888;
	width: 80%;
	z-index: 2;
}

.navmodal_navigation {
	list-style: none;
	padding: 0;
	margin: 0;
}

.navmodal_navigation ul {
	display: flex;
	flex-direction: column;
}

.navmodal_navigation li {
	margin-bottom: 10px;
	list-style: none;
}

.navmodal_navigation a {
	text-decoration: none;
	color: black;
}

.navmodal_menu-toggle {
	cursor: pointer;
	display: flex;
	flex-direction: column;
}

.navmodal_bar {
	width: 25px;
	height: 3px;
	background-color: #6a6a6a;
	margin: 5px 0;
}

.navmodal_close {
	color: #aaa;
	float: right;
	font-size: 28px;
	font-weight: bold;
}

.navmodal_close:hover,
.navmodal_close:focus {
	color: black;
	text-decoration: none;
	cursor: pointer;
}

@media only screen and (max-width: 1000px) {
	.navmodal_modal_contact-info {
		display: none;
	}
}

@media only screen and (max-width: 1000px) {
	.navmodal_navigation-wrapper {
		display: flex;
	}
}
