@import "https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap";
body {
  font-family: Lato, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.header nav {
  text-transform: uppercase;
  font-size: 12px;
}

.logo img {
  max-height: 50px;
}

.navigation-wrapper {
  align-items: center;
  display: none;
}

.navigation ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation ul li {
  margin-right: 20px;
}

.navigation ul li:last-child {
  margin-right: 0;
}

.navigation ul li a {
  color: #333;
  padding: 10px;
  font-weight: bold;
  text-decoration: none;
}

.navigation ul li a:hover {
  text-decoration: underline;
}

.contact-info {
  color: #333;
  flex-direction: column;
  align-items: flex-end;
  margin-left: 20px;
  display: flex;
}

.page-header__contacts-item {
  color: #333;
  align-items: center;
  padding: 0 0 5px;
  text-decoration: none;
  display: flex;
}

.page-header__contacts-item:hover {
  color: #00f;
}

.page-header__contacts-item svg {
  fill: currentColor;
  width: 16px;
  height: 12px;
  margin-right: 5px;
}

@media screen and (min-width: 1000px) {
  .navigation-wrapper {
    display: flex;
  }
}

.footer {
  text-align: center;
  background-color: #fff;
  padding: 20px;
}

.footer img {
  max-width: 150px;
}

@media screen and (min-width: 1200px) {
  .logo img {
    max-height: 75px;
  }

  .footer img {
    max-width: 250px;
  }
}

.navmodal_navigation-wrapper {
  z-index: 2;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: none;
}

.navmodal {
  z-index: 1;
  background-color: #000000b3;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.navmodal-content {
  z-index: 2;
  background-color: #fefefe;
  border: 1px solid #888;
  width: 80%;
  margin: 15% auto;
  padding: 20px;
}

.navmodal_navigation {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navmodal_navigation ul {
  flex-direction: column;
  display: flex;
}

.navmodal_navigation li {
  margin-bottom: 10px;
  list-style: none;
}

.navmodal_navigation a {
  color: #000;
  text-decoration: none;
}

.navmodal_menu-toggle {
  cursor: pointer;
  flex-direction: column;
  display: flex;
}

.navmodal_bar {
  background-color: #6a6a6a;
  width: 25px;
  height: 3px;
  margin: 5px 0;
}

.navmodal_close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.navmodal_close:hover, .navmodal_close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

@media only screen and (max-width: 1000px) {
  .navmodal_modal_contact-info {
    display: none;
  }

  .navmodal_navigation-wrapper {
    display: flex;
  }
}

